section.map-info{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #daff9b00;
    padding: 20px 10px;
}

section > div{
    display: grid;
    grid-template-columns: 1fr minmax(220px, min-content);
    grid-template-rows: 300px;
    width: 100%;
    max-width: 768px;
    border-radius: 7px;
}

.map-container{
    position: relative;
    user-select: none;
}

.ol-viewport{
    border-radius: 7px;
}

.map-container .pelicula {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 1;
}

.map-container .pelicula .pingo{
    width: 14px;
    height: 14px;
    background-color: red;
    border-radius: 7px;
}

.map-info > div .info{
    padding: 0 10px;
    background-color: rgba(0, 26, 255, 0);
}

.info > div{
    margin: 10px 0;
}

.info > div span:nth-of-type(1){
    font-weight: 400;
}

.info > div span:nth-of-type(2){
    color: var(--orange-color);
    font-weight: 600;
    user-select: all;
}



@media screen and (max-width: 768px) {
    section > div{
        grid-template-columns: 1fr;
    }
}