:root{
  --orange-color: rgb(255, 138, 29);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}
body,html{
  height: 100%;
}

#root{
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 80px min-content 1fr 60px;
}

.shadow{
  box-shadow: 0px 0px 6px 0px rgba(0,0,0,0.75);
}