header{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(220, 20, 60, 0);
    border-bottom: 1px solid rgba(0, 0, 0, 0.100);
    text-align: center;
}

header h1 {
    font-size: 80px;
    letter-spacing: 20px;
    font-family: 'Zen Loop', cursive;
    font-weight: 300;
}


@media screen and (max-width: 450px) {
   header h1{
       font-size: 50px;
       letter-spacing: 15px;
   }
}

@media screen and (max-width: 300px) {
    header h1{
        font-size: 40px;
    }
}